@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);

body
{
    margin: 0 auto;
}
.mainList
{
    display: flex;
    align-items: center;
    
    border: solid 20px #00FF96; 
    border-top: 0;
    border-bottom: 0;
    padding: 100px;
    
}
.topicList
{
   
    font-family: 'Roboto', sans-serif;
    word-spacing: 0px;
    text-align:center;
    width: 25%;

    
    
    
    
    
}

.imgTopic
{
   width: 200px;
   height: 200px;
   border: solid 2px black;
   margin: 0;
   margin-bottom: 5px;
   border-radius: 50%;
   
   
   
   
}
.h1Topic
{
    font-size: 45px;
    margin-top: 5px;
    margin-bottom: 5px;
    font-weight: 700;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
.pTopic

{

    margin-bottom: 20px;
    margin-top: 5px;
    font-weight: 400;
    font-size: 25px;

}
.buttonTopic
{
    text-decoration: none;
    color: #00FF96;
    background-color: #125e54;
    border: solid 2px #00FF96;
    padding: 10px;
    border-radius: 20%;
    
    
    
}
.header
{
    height: 100px;
    background-color: #00FF96;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
}
.footer
{
   
    height:200px;
   
    background-color: #125e54;
    display:flex;
    align-items:center;
    justify-content: center;
    border: solid 20px #00FF96; 
    border-top: 0;
    
    

}
.footerImage
{
    width: 60px;
    margin: 10px;
    border-radius: 50%;
    margin: 50px;
    
}


.startH
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    font-size: 120px;
    font-weight: 700;
    text-align: center;
    margin-top: 0;
    padding: 50px;
}
.startP
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    font-size: 120px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 0;
    padding: 50px;
}
.start
{
    background-image: url(/monk-windwalker-guide/static/media/jade_forest.f63b45e5.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    border: solid 20px #00FF96; 
    
    border-bottom: 0;
}
body::-webkit-scrollbar 
{
    background-color: #00FF96;
    
}
body::-webkit-scrollbar-track
{
    background-color: #00FF96;
}
body::-webkit-scrollbar-thumb
{
    background-color: #125e54;
    
}
.header
{
    display:flex;
    align-items: center;
    justify-content: center;
    
}
.menuH
{
    color:#125e54;
    font-weight: 700;
    text-shadow: -1px 0 #125e54, 0 1px , 1px 0 #125e54, 0 -1px #125e54;
    padding-left: 100px;
    font-size: 42px;
   
}
.menuItems
{
    text-decoration: none;
}
